* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

#root{
    display: flex;
    justify-content: center;
}

.App {
    width: 800px;
}

.post {
    display: flex;
    padding: 15px;
    border: 2px solid sienna;
    margin-top: 15px;
    justify-content: space-between;
    align-items: center;
}

.post-enter {
    transform: translateX(-350px);
}
.post-enter-active {
    /*в случае добавления элемента будет выдвигаться на 350 px*/
    transform: translateX(0px);
    transition: all 500ms ease-in;
}
.post-exit {
    opacity: 1;
}
.post-exit-active {
    /*в случае удаления элемента будет сдвигаться на -350px*/
    transform: translateX(-350px);
    transition: all 500ms ease-in;
}